.settings > span {
  display: block;
  padding: 2em 0;
}
.settings > span .inline-btn {
  all: unset;
  font-weight: 700;
}

.settings ul {
  all: unset;
}

.containList {
  margin: 0 auto;
  max-width: 80%;
  height: 40vh;
}

[data-theme=dark] .holdAllWords {
  background-color: rgba(59, 68, 75, 0.548);
}

.holdAllWords {
  display: flex;
  width: 100%;
  align-content: center;
  background-color: rgba(220, 224, 224, 0.642);
  max-height: 100%;
  overflow-y: auto;
  overflow-x: auto;
}

.holdAllWords ul {
  width: 100%;
}

.itemWord {
  width: 80%;
}

.word {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5em 1em;
}
.word .save {
  display: none;
}
.word.editing .itemWord {
  padding: 0.5em 0 0.5em 0;
  transition: all 0.15s ease-in-out;
  background: rgba(138, 137, 137, 0.3);
}
.word.editing .save {
  display: initial;
}
.word.editing .edit {
  display: none;
}/*# sourceMappingURL=style.css.map */